.create-project-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}

.create-project-container {
  width: 40%;
}

.create-project-form {
  width: 100%;
  padding: 1.5rem;
  gap: 1rem;
}

.create-project-form h1 {
  border-bottom: solid;
  line-height: 3rem;
}

.create-project-item {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.create-project-item:last-child {
  margin-top: 1.3rem;
}

@media screen and (max-width:1024px) {
  .create-project-container {
    width: 80%;
  }
}